___CSS_LOADER_EXPORT___.push([module.id, `* {
    padding: 0;
    margin: 0;
    box-sizing: border-box;
    --medium-icon-size: 30px
}

div {
    padding: 8px;
}

body {
    background-color: #ffffff;
    min-height: 100vh;
    display: flex;
    overflow-y: hidden;
    overflow-x: auto;
}

.sidebar {
    min-width: 180px;
    background-color: #f6f6f6;
    flex: 18%;
}

.main-content {
    padding: 0;
    flex: 82%;
    display: flex;
    flex-direction: column;
}

button {
    cursor: pointer;
}

ul {
    /* padding-left: 20px; */
    font-weight: 600;
    list-style: none;
    display: flex;
    flex-direction: column;
    gap: 10px;
}

li {
    display: flex;
}

.nav {
    flex: auto;
    display: flex;
    align-items: center;
    justify-content: flex-start;
    gap: 10px;
    border: none;
    padding: 12px 12px;
    border-radius: 6px;
    font-size: 16px;
    background-color: #f6f6f6;
}

.nav:hover {
    background-color: #f0f0f0;
}

.nav > img {
    width: var(--medium-icon-size);
}

.priority {
    font-weight: 600;
}

