    display: flex;
}

.todo {
    padding: 4px;
    border-radius: 8px;
    background-color: #ffdedead;
    font-size: 14px;
    display: grid;
    grid-template-columns: repeat(3fr);
    grid-template-rows: repeat(2fr);
    width: 400px;
}

.todo-title {
    font-size: 16px;
    font-weight: 600;
    color: #444444;
}

.todo-description {
    font-style: italic;
    grid-column:  1 / 4;
}

.todo-duedate {
    grid-column: 2;
    color: rgb(64, 165, 64);
}

.todo-priority {
    grid-column: 3;
    color: rgb(74, 74, 214);
}

.project-section {
    flex: auto;
    display: flex;
    /* align-items: center; */
    gap: 12px;
}

.projectlist {
    display: flex;
    justify-content: space-between;
    gap: 2px;
}

.project {
    width: 300px;
    display: flex;
    flex-direction: column;
    max-height: 95vh;
    /* border-radius: 10px; */
    /* flex: auto; */
    border-right: 1px solid rgb(199, 199, 199);
}

.project-name {
    display: flex;
    align-items: center;
    justify-content: space-between;
}

.project-name > h3 {
    border-radius: 4px;
    padding: 0 4px;
    text-overflow: ellipsis;
    overflow: hidden;
}

.project:hover {
    /* border: 1px solid #FFF; */
}

.tasks {
    display: flex;
    flex-direction: column;
    gap: 8px;
    /* width: 100%;
    height: 100%; */
    overflow-y: scroll;
    scrollbar-width: none;
}

.task {
    padding: 2px;
    display: flex;
    flex-direction: column;
    /* border: 1px solid red; */
}

.task-collapsed, .task-left {
    padding: 0px;
    display: flex;
    align-items: center;
    justify-content: space-between;
    flex-wrap: nowrap;
}

.task-collapsed > div {
    padding: 0%;
}

.task-expanded {
    font-size: 13px;
    padding: 0;
    margin-top: 4px;
    display: flex;
    flex-direction: column;
    flex-wrap: wrap;
    max-height: 0;
    transform: scaleY(0);
    transform-origin: top;
    transition: transform 0.3s ease, max-height 0.4s ease;
}

.task.expandable  .task-expanded {
    max-height: 350px;
    transform: scaleY(1);
}

.task-expanded > div {
    padding: 1px 0;
}

.task-name {
    display: flex;
    gap: 4px;
    /* white-space: nowrap;
    overflow: hidden;
    text-overflow: ellipsis; */
}

.task-exp-collapsed {
    font-size: 11px;
}

.task-exp-date {
    font-weight: 600;
    color: orange;
}

.non-editable > div {
    padding: 0;
}

.task-creation-date, .task-id {
    color: #9c9c9c;
    font-size: 11px;
}


.caret, .task-icon {
    height: 20px;
    width: 20px;
    border: none;
    border-radius: 50%;
    background-color: #FFF;
    transition: background-color 0.3s ease-in-out;
}

.caret:hover, .project-icon:hover, .task-icon:hover {
    /* width: 18px;
    height: 18px; */
    background-color: #ebebeb;
    opacity: 0.7;
    /* border: 2px solid #FFF; */
    transition: 0.5s;
}

.caret-down {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_0___});
}

.caret-up {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_1___});
}

.delete-icon {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_2___});
    opacity: 0.2;
}

.close-icon {
    background-image: url(${___CSS_LOADER_URL_REPLACEMENT_3___});
    opacity: 0.2;
}

.close-icon, .delete-icon {
    border-radius: 20%;
}

.project-icon {
    height: var(--medium-icon-size);
    width: var(--medium-icon-size);;
    border: none;
    background-color: #FFF;
    transition: background-color 0.3s ease-in-out;
}

.addproj {
    padding: 4px;
    font-size: 12px;
    border: none;
    background-color: #FFF;
    border-radius: 10px;
    align-self: center;
    transition: background-color 0.3s ease-in-out;
}

.addproj:hover {
    background-color: #eeeeeeaf;
    /* border: 1px solid #FFF; */
    padding: 5px;
}

.addproj > img {
    width: 40px;
}

.addproj > div {
    padding: 0;
}

.add-task {
    display: flex;
    justify-content: center;
}

.add-task > button {
    padding: 2px 10px;
    width: 50%;
}

input[type="text"], input[type="date"] {
    border: none;
    border-radius: 8px;
    padding: 2px;
}

input[type="date"] {
    font-size: 13px;
}

input[type="text"]:focus, input[type="date"]:focus {
    outline: 1.5px solid #a1a1a1;
}

input[type="checkbox"]:checked {
    accent-color: rgb(44, 158, 44);
}

textarea {
    border: none;
    max-height: 100px;
    border-radius: 4px;
    resize: none;
    overflow-y: hidden;
}

textarea:focus {
    outline: 1.5px solid #a1a1a1;
}

select {
    border-radius: 20%;
    background-color: #FFF;
    padding: 2px 4px;
    border: none;
}

select:hover {
    background-color: #e4e4e4;
}`, "",{"version":3,"sources":["webpack://./src/projects/projects.css"],"names":[],"mappings":"AAAA;IACI,aAAa;AACjB;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,2BAA2B;IAC3B,eAAe;IACf,aAAa;IACb,kCAAkC;IAClC,+BAA+B;IAC/B,YAAY;AAChB;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,cAAc;AAClB;;AAEA;IACI,kBAAkB;IAClB,mBAAmB;AACvB;;AAEA;IACI,cAAc;IACd,uBAAuB;AAC3B;;AAEA;IACI,cAAc;IACd,uBAAuB;AAC3B;;AAEA;IACI,UAAU;IACV,aAAa;IACb,yBAAyB;IACzB,SAAS;AACb;;AAEA;IACI,aAAa;IACb,8BAA8B;IAC9B,QAAQ;AACZ;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,gBAAgB;IAChB,yBAAyB;IACzB,gBAAgB;IAChB,0CAA0C;AAC9C;;AAEA;IACI,aAAa;IACb,mBAAmB;IACnB,8BAA8B;AAClC;;AAEA;IACI,kBAAkB;IAClB,cAAc;IACd,uBAAuB;IACvB,gBAAgB;AACpB;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,aAAa;IACb,sBAAsB;IACtB,QAAQ;IACR;mBACe;IACf,kBAAkB;IAClB,qBAAqB;AACzB;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,sBAAsB;IACtB,2BAA2B;AAC/B;;AAEA;IACI,YAAY;IACZ,aAAa;IACb,mBAAmB;IACnB,8BAA8B;IAC9B,iBAAiB;AACrB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,eAAe;IACf,UAAU;IACV,eAAe;IACf,aAAa;IACb,sBAAsB;IACtB,eAAe;IACf,aAAa;IACb,oBAAoB;IACpB,qBAAqB;IACrB,qDAAqD;AACzD;;AAEA;IACI,iBAAiB;IACjB,oBAAoB;AACxB;;AAEA;IACI,cAAc;AAClB;;AAEA;IACI,aAAa;IACb,QAAQ;IACR;;8BAE0B;AAC9B;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,gBAAgB;IAChB,aAAa;AACjB;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,cAAc;IACd,eAAe;AACnB;;;AAGA;IACI,YAAY;IACZ,WAAW;IACX,YAAY;IACZ,kBAAkB;IAClB,sBAAsB;IACtB,6CAA6C;AACjD;;AAEA;IACI;mBACe;IACf,yBAAyB;IACzB,YAAY;IACZ,4BAA4B;IAC5B,gBAAgB;AACpB;;AAEA;IACI,yDAA2D;AAC/D;;AAEA;IACI,yDAAyD;AAC7D;;AAEA;IACI,yDAAqD;IACrD,YAAY;AAChB;;AAEA;IACI,yDAAoD;IACpD,YAAY;AAChB;;AAEA;IACI,kBAAkB;AACtB;;AAEA;IACI,+BAA+B;IAC/B,8BAA8B;IAC9B,YAAY;IACZ,sBAAsB;IACtB,6CAA6C;AACjD;;AAEA;IACI,YAAY;IACZ,eAAe;IACf,YAAY;IACZ,sBAAsB;IACtB,mBAAmB;IACnB,kBAAkB;IAClB,6CAA6C;AACjD;;AAEA;IACI,2BAA2B;IAC3B,4BAA4B;IAC5B,YAAY;AAChB;;AAEA;IACI,WAAW;AACf;;AAEA;IACI,UAAU;AACd;;AAEA;IACI,aAAa;IACb,uBAAuB;AAC3B;;AAEA;IACI,iBAAiB;IACjB,UAAU;AACd;;AAEA;IACI,YAAY;IACZ,kBAAkB;IAClB,YAAY;AAChB;;AAEA;IACI,eAAe;AACnB;;AAEA;IACI,4BAA4B;AAChC;;AAEA;IACI,8BAA8B;AAClC;;AAEA;